<template>
  <div>
    <el-container>
      <el-container>

        <el-main>
          <div class="box">
            <div class="container">
              <div class="container1">
                <el-row class="ep_info">

                  <el-table  :data="Data" style="border: 1px solid black;margin-bottom: 50px">

                    <el-table-column  label="省">
                      <template slot-scope="scope">
                        <span class="col-cont" v-html="showDate(scope.row.name)" ></span>
                      </template>

                    </el-table-column>
                    <el-table-column  label="市">
                      <template slot-scope="scope">
                        <span class="col-cont" v-html="showDate(scope.row.gender)" ></span>
                      </template>
                    </el-table-column>
                    <el-table-column  label="县/区">
                      <template slot-scope="scope">
                        <span class="col-cont" v-html="showDate(scope.row.phone_number)" ></span>
                      </template>
                    </el-table-column>
                    <el-table-column  label="街道">
                      <template slot-scope="scope">
                        <span class="col-cont" v-html="showDate(scope.row.email)" ></span>
                      </template>
                    </el-table-column>
                    <el-table-column label="地址" >
                      <template slot-scope="scope">
                        <span class="col-cont" v-html="showDate(scope.row.id)" ></span>
                      </template>
                    </el-table-column>
                  </el-table>
                </el-row>
                <el-row>
                  <el-form ref="commentFrom"  label-width="80px" class="padding">
                    <el-form-item label="省" prop="worker_id">
                      <el-input ></el-input>
                    </el-form-item>

                    <el-form-item label="市" prop="staff_id">
                      <el-input ></el-input>
                    </el-form-item>

                    
                    <el-form-item label="详情" prop="detail">
                      <el-input type="textarea" ></el-input>
                    </el-form-item>
                    <el-form-item>
                      <el-button type="primary" >提交</el-button>
                      <el-button>取消</el-button>
                    </el-form-item>
                  </el-form>
                </el-row>
              </div>
            </div>
          </div>
        </el-main>
      </el-container>
    </el-container>

  </div>

</template>

<script>
export default {
name: "receivingAddress"
}
</script>

<style scoped>

</style>



